/**
 * useSettingsScreen Hook Tests
 * File: useSettingsScreen.test.js
 */
const { renderHook, act } = require('@testing-library/react');
const { useRouter, usePathname } = require('expo-router');

jest.mock('expo-router', () => ({
  useRouter: jest.fn(),
  usePathname: jest.fn(),
}));

const useSettingsScreen = require('@platform/screens/settings/SettingsScreen/useSettingsScreen').default;
const { SETTINGS_TABS } = require('@platform/screens/settings/SettingsScreen/types');

describe('useSettingsScreen Hook', () => {
  const mockPush = jest.fn();

  beforeEach(() => {
    jest.clearAllMocks();
    useRouter.mockReturnValue({ push: mockPush });
    usePathname.mockReturnValue('/settings/tenants');
  });

  describe('initialization', () => {
    it('returns initial state', () => {
      const { result } = renderHook(() => useSettingsScreen());
      expect(result.current.selectedTab).toBeDefined();
      expect(result.current.tabs).toBeDefined();
      expect(result.current.onTabChange).toBeDefined();
    });

    it('provides all tab definitions', () => {
      const { result } = renderHook(() => useSettingsScreen());
      expect(result.current.tabs.length).toBeGreaterThan(0);
      expect(result.current.tabs[0]).toHaveProperty('id');
      expect(result.current.tabs[0]).toHaveProperty('label');
      expect(result.current.tabs[0]).toHaveProperty('testID');
    });

    it('provides testID and accessibilityLabel', () => {
      const { result } = renderHook(() => useSettingsScreen());
      expect(result.current.testID).toBe('settings-screen');
      expect(result.current.accessibilityLabel).toBe('settings.screen.label');
    });
  });

  describe('tab changes', () => {
    it('handles tab changes', () => {
      const { result } = renderHook(() => useSettingsScreen());
      
      act(() => {
        result.current.onTabChange(SETTINGS_TABS.USER);
      });

      expect(mockPush).toHaveBeenCalled();
    });

    it('navigates to correct route on tab change', () => {
      const { result } = renderHook(() => useSettingsScreen());
      
      act(() => {
        result.current.onTabChange(SETTINGS_TABS.ROLE);
      });

      expect(mockPush).toHaveBeenCalledWith('/settings/roles');
    });

    it('handles all tab navigation', () => {
      const { result } = renderHook(() => useSettingsScreen());
      
      const tabs = [
        { id: SETTINGS_TABS.USER, route: '/settings/users' },
        { id: SETTINGS_TABS.ROLE, route: '/settings/roles' },
        { id: SETTINGS_TABS.PERMISSION, route: '/settings/permissions' },
        { id: SETTINGS_TABS.TENANT, route: '/settings/tenants' },
      ];

      tabs.forEach(({ id, route }) => {
        mockPush.mockClear();
        act(() => {
          result.current.onTabChange(id);
        });
        expect(mockPush).toHaveBeenCalledWith(route);
      });
    });
  });

  describe('pathname detection', () => {
    it('detects current tab from pathname', () => {
      usePathname.mockReturnValue('/settings/users');
      const { result } = renderHook(() => useSettingsScreen());
      expect(result.current.selectedTab).toBe(SETTINGS_TABS.USER);
    });

    it('handles various pathname patterns', () => {
      const tests = [
        { pathname: '/settings/tenants', expectedTab: SETTINGS_TABS.TENANT },
        { pathname: '/settings/roles', expectedTab: SETTINGS_TABS.ROLE },
        { pathname: '/settings/users', expectedTab: SETTINGS_TABS.USER },
      ];

      tests.forEach(({ pathname, expectedTab }) => {
        usePathname.mockReturnValue(pathname);
        const { result } = renderHook(() => useSettingsScreen());
        expect(result.current.selectedTab).toBe(expectedTab);
      });
    });
  });
});
